<div class="main-info" v-if="movie"> <img :src="imageFullUrl + movie.backdrop_path" alt="alt"><div class="main-info-block"> <div class="main-info-content"><div class="main-info-content-block"> <h2>{{movie.title}}</h2><p>{{movie.overview}}</p><p class="date"> <span>{{new Date(movie.release_date).getFullYear() }},</span><span v-for="genre in movie.genres" :key="genre.id">  {{genre.name}}</span></p><div class="actors"> </div></div></div></div></div>